<template>
    <div class="hot">
        <ul>
            <li>新闻标题</li>
            <li v-for="(item, index) in list" :key="index">
                <span>{{item}}</span>
                <el-button type="danger" plain @click="del(index)">删除</el-button>
            </li>
            <li>
                <el-input v-model="input" placeholder="请输入新闻" class="box"></el-input>
                <el-button type="primary" round @click="add">添加</el-button>
            </li>
        </ul>

        <el-button type="success" class="indexBtn" @click="back">返回首页</el-button>
    </div>
</template>

<script>
export default {
    name: 'Saishi',
    data() {
        return {
            input: '',
        }
    },
    computed: {
        list() {
            return this.$store.state.newsAbout.news[5];
        }
    },
    methods: {
        // 删除新闻
        del(index) {
            this.$store.commit('newsAbout/DEL', [5, this.list[index]]);
        },
        // 添加新闻
        add() {
            if(this.input) {
                this.$store.commit('newsAbout/ADD', [5, this.input]);
                this.input = '';
            }
        },
        // 返回首页
        back() {
            this.$router.push('/index');
        }
    },
}
</script>

